import Vue from 'vue';
import Router from 'vue-router';
import Index from '@/components/index/Index';

Vue.use(Router);

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Index',
      component: Index,
    },
    {
      path: '/assets',
      name: 'Assets',
      component: Index,
    },
    {
      path: '/income',
      name: 'Income',
      component: Index,
    },
  ],
});



